const $siteList = $('.siteList');
const $lastLi = $siteList.find('li.last');

const x = localStorage.getItem('x');
const xObject = JSON.parse(x);
const hashMap = xObject ||
    [
        { logo: 'B', url: 'https://www.bilibili.com' },
        { logo: 'C', url: 'https://www.ctrip.com/' },
        { logo: 'T', url: 'https://www.taobao.com/' }

    ] //ç”¨hashmapå‚¨å­˜é¢„è§ˆç»“æž„

const simplifyUrl = (url) => {
    return url.replace('https://', '')
        .replace('http://', '')
        .replace('www.', '')
        .replace(/\/.*/, '')
};


const render = () => { //æ¸²æŸ“é¡µé¢å‡½æ•°
    $siteList.find('li:not(.last').remove(); //æ¸²æŸ“htmlä¹‹å‰è¦æŠŠä¹‹å‰çš„é¡µé¢éƒ½æ¸…é™¤

    hashMap.forEach((node, index) => {
        const $li = $(`<li>
         
           <div class="site">
             <div class="logo">${node.logo[0]}</div>               
             <div class="link">${simplifyUrl(node.url)}</div>
             <div class="delete">x</div>
             <svg class="icon" aria-hidden="true">
             <use xlink:href="#icon-close1"></use>
         </svg>
           </div>
          
       </li>`).insertBefore($lastLi)

        $li.on('click', () => {
            window.open(node.url)
        });

        $li.on('click', '.delete', (e) => {
            e.stopPropagation();
            hashMap.splice(index, 1); //ç‚¹å‡»xå°±åˆ é™¤è¿™ä¸ªindexç´¢å¼•ä½ç½®çš„ç½‘ç«™
            //é‡æ–°æ¸²æŸ“
            render();
        })
    });
};

//åœ¨addsiteä¹‹å‰å…ˆrenderé¢„è§ˆé¡µé¢
render();

//ç„¶åŽè¿›è¡Œç‚¹å‡»addsiteçš„åŠŸèƒ½
$('.addSite')
    .on('click', () => {
        let url = window.prompt('æ‚¨è¦æ·»åŠ çš„ç½‘å€æ˜¯ï¼š');
        if (url.indexOf('http') !== 0) {
            url = 'https://' + url;
        }
        console.log(url);
        hashMap.push({
            logo: simplifyUrl(url)[0],

            url: url
        }); //èŽ·å–æœ€æ–°çš„hashmapæ•°ç»„

        render(); //é‡æ–°æ¸²æŸ“é¡µé¢
    });


window.onbeforeunload = () => {
    const string = JSON.stringify(hashMap);
    localStorage.setItem('x', string); //æŠŠhashmapè®¾ç½®ä¸ºstringèµ‹å€¼ç»™x
}

